import React from 'react';
import {Container, Stack} from "@mui/material";
import styles from './index.module.css'
import Title from "../../ui/Title";
import FirstImg from '../../assets/images/why/uzrpy3alwh0rnv165bqrtxdc5x73jelg.png'
import SecondImg from '../../assets/images/why/oz4u1zv5wz59pg9oizjpha242bnwry0k.png'
import ThirdImg from '../../assets/images/why/ot1nkj7kwawdt4mia6fpimvbtqn8u2tx.png'


const UnderBanner = () => {
		return (
				<section className={styles.root}>
						<div className={styles.top}>
								<Container>
										<h3 className={styles.topText}>        .  
												  ,  
												 !</h3>
								</Container>
						</div>

						<div className={styles.bottom}>
								<Container>
										<Stack alignItems='center' justifyContent='center' spacing={4}>
												<Title textMain=' ' textSecondary='CRYSTAL?'/>
												<Stack direction='row' flexWrap='wrap' justifyContent='center' spacing={1} rowGap='40px'>
														<Stack alignItems='center' justifyContent='center' spacing={2}>
																<img src={FirstImg} className={styles.img} height={130} width={99} alt="why"/>
																<span className={styles.why__top}> </span>
																<span className={styles.why__bottom}>    Chemspec.</span>
														</Stack>
														<Stack alignItems='center' justifyContent='center' spacing={2}>
																<img src={SecondImg} className={styles.img} height={130} width={99} alt="why"/>
																<span className={styles.why__top}></span>
																<span className={styles.why__bottom}>     .</span>
														</Stack>
														<Stack alignItems='center' justifyContent='center' spacing={2}>
																<img src={ThirdImg} className={styles.img} height={130} width={99} alt="why"/>
																<span className={styles.why__top}> </span>
																<span className={styles.why__bottom}> 30      .</span>
														</Stack>
												</Stack>
										</Stack>
								</Container>
						</div>

				</section>
		);
};

export default UnderBanner;